import React, {useEffect} from 'react';

import {Panel, PanelHeader, Slider} from '@vkontakte/vkui/';

import fire_fon_img from '../img/fire_fon_img.jpg'

import './Song.css'

const Song = ({ id, volume, setVolume, setPlaer  }) => {

	useEffect(() => {
		setPlaer()

		return () => {
			setPlaer()
		}
	}, [])

	return (
		<Panel id={id}>
		<PanelHeader>Плеер</PanelHeader>
		<div className='song-menu' style={{backgroundImage: `url(${fire_fon_img})`}}>
			<div className='equalizer-div'>
			<button className="equalizer">
   				<span className="eq1"></span>
   				<span className="eq2"></span>
   				<span className="eq3"></span>
				<span className="eq4"></span>
				<span className="eq5"></span>
			</button>
			</div>
		</div>

		<Slider
                  step={1}
                  min={0}
                  max={100}
                  value={Number(volume * 100)}
                  onChange={valuen=> setVolume(valuen / 100)}
                />
		
		<div className='active-button' onClick={() => {
			setPlaer()
		}}></div>
		</Panel>
	)
} 


export default Song;